export const generateTreatmentRecommendations = (
  _query: string, 
  species: PetSpecies, 
  symptomAnalysis: SymptomAnalysis
): TreatmentRecommendation => {
  const speciesInfo = petSpeciesInfo[species];
  let assessment = `Pamatojoties uz jsu aprakstu par ${speciesInfo.name}, `;
  let possibleCauses: string[] = [];
  let recommendations: string[] = [];
  let suggestedMedicines: string[] = [];
  let dietaryAdvice: string[] = [];

  // Generate recommendations based on symptom category
  switch (symptomAnalysis.category) {
    case 'skin':
      assessment += 'iet, ka jsu mjdzvniekam ir das vai spalvas problmas.';
      possibleCauses = [
        'Aleriskas reakcijas (prtika, vides faktori)',
        'Parazti (bluses, rces, das rces)',
        'Hormonu disbalanss',
        'Stress vai uzvedbas problmas',
        'Snu vai bakterilu infekciju',
        'Uztura trkums (omega-3, vitamni)'
      ];
      recommendations = [
        'Prbaudiet, vai nav paraztu (bluses, rces)',
        'Novrtjiet pdjs izmaias uztur vai vid',
        'Nodroiniet kvalitatvu uzturu ar omega-3 taukskbm',
        'Maziniet stresa faktorus',
        'Regulri emmjiet un prbaudiet du'
      ];
      suggestedMedicines = ['omega-3', 'frontline-plus', 'chlorhexidine-shampoo', 'aloe-vera-gel'];
      dietaryAdvice = [
        'Pievienojiet omega-3 taukskbes uztur',
        'Izvairieties no zinmiem alerniem',
        'Nodroiniet augstas kvalittes olbaltumvielas'
      ];
      break;

    case 'digestive':
      assessment += 'ir novrojamas gremoanas sistmas problmas.';
      possibleCauses = [
        'Uztura nepanesamba vai alerijas',
        'Zarnu infekcijas vai parazti',
        'Stress vai vides izmaias',
        'Barbas kvalittes problmas',
        'Kua-zarnu trakta iekaisums',
        'Toksnu iedarbba'
      ];
      recommendations = [
        'Nodroiniet pietiekamu idruma daudzumu',
        'Piedvjiet viegli sagremojamu barbu',
        'Uzraugiet simptomu izmaias',
        'Izvairieties no barbas izmaim',
        'Nodroiniet klusu, drou vidi'
      ];
      suggestedMedicines = ['probiotics-paste', 'famotidine', 'psyllium', 'royal-canin-digestive'];
      dietaryAdvice = [
        'Piedvjiet mazas, bieas porcijas',
        'Izmantojiet viegli sagremojamu barbu',
        'Pievienojiet probiotikus'
      ];
      break;

    case 'respiratory':
      assessment += 'ir novrojamas elpoanas sistmas problmas.';
      possibleCauses = [
        'Augjo elpceu infekcijas',
        'Aleriskas reakcijas',
        'rjie kairintji (dmi, puteki)',
        'Sirds problmas',
        'Plauu infekcijas',
        'Elpceu obstrukcija'
      ];
      recommendations = [
        'Nodroiniet tru, mitru gaisu',
        'Izvairieties no kairintjiem',
        'Uzraugiet elpoanas bieumu',
        'Nodroiniet klusu vidi',
        'Nekavjoties vrsieties pie veterinrrsta'
      ];
      suggestedMedicines = ['vitamin-c', 'lysine'];
      dietaryAdvice = [
        'Nodroiniet vieglu, uzturvielm bagtu barbu',
        'Pievienojiet imunitti stiprinous papildinjumus'
      ];
      break;

    case 'urinary':
      assessment += 'ir novrojamas urnceu problmas.';
      possibleCauses = [
        'Urnceu infekcijas',
        'Urnpa akmei',
        'Nieru problmas',
        'Diabts',
        'Stress',
        'Nepietiekams idruma daudzums'
      ];
      recommendations = [
        'Nodroiniet pastvgu piekuvi tram denim',
        'Uzraugiet urinanas bieumu',
        'Prbaudiet urna krsu un smaru',
        'Samaziniet stresa faktorus',
        'Konsultjieties ar veterinrrstu'
      ];
      suggestedMedicines = ['cranberry-extract', 'hills-urinary-care'];
      dietaryAdvice = [
        'Palieliniet idruma patriu',
        'Izmantojiet urnceu veselbai paredztu barbu',
        'Samaziniet minerlvielu daudzumu'
      ];
      break;

    case 'behavioral':
      assessment += 'ir novrojamas uzvedbas izmaias.';
      possibleCauses = [
        'Slimba vai spes',
        'Stress vai trauksme',
        'Vides izmaias',
        'Vecuma saisttas izmaias',
        'Hormonu izmaias',
        'Neirologiskas problmas'
      ];
      recommendations = [
        'Novrojiet citus simptomus',
        'Nodroiniet klusu, drou vidi',
        'Saglabjiet ierasto rutnu',
        'Prbaudiet fizisko aktivitti',
        'Dokumentjiet izmaias'
      ];
      suggestedMedicines = ['vitamin-b-complex', 'probiotics-paste'];
      dietaryAdvice = [
        'Nodroiniet viegli sagremojamu barbu',
        'Pievienojiet probiotikus stresa mazinanai'
      ];
      break;

    default:
      assessment += 'ir nepiecieama skka analze.';
      possibleCauses = [
        'Dadi faktori var ietekmt mjdzvnieka veselbu',
        'Nepiecieama detaliztka simptomu novrtana'
      ];
      recommendations = [
        'Detalizti aprakstiet visus novrotos simptomus',
        'Fiksjiet simptomu ilgumu un intensitti',
        'Novrtjiet anas un dzeranas paradumus',
        'Prbaudiet fizisko aktivitti'
      ];
      suggestedMedicines = ['vitamin-b-complex', 'multivitamin'];
      dietaryAdvice = ['Nodroiniet kvalitatvu, sabalanstu uzturu'];
  }

  const whenToSeeVet = symptomAnalysis.urgencyLevel === 'emergency' ? 
    'Nekavjoties vrsieties pie veterinrrsta!' :
    symptomAnalysis.urgencyLevel === 'high' ?
    'Ieteicams konsultties ar veterinrrstu odien vai rt.' :
    symptomAnalysis.urgencyLevel === 'medium' ?
    'Ieteicams konsultties ar veterinrrstu 1-2 dienu laik.' :
    'Ja simptomi turpins vai pasliktins, konsultjieties ar veterinrrstu.';

  return {
    assessment,
    possibleCauses,
    recommendations,
    suggestedMedicines,
    dietaryAdvice,
    whenToSeeVet,
    urgencyLevel: symptomAnalysis.urgencyLevel
  };
};